<template>
	<div>
		<back-header theme="确认订单"></back-header>
		<div class="center-wrap">
			<p class="bg">请核对订单信息</p>
			<ul>
				<li v-for="(item,index) of orderList">
					<div class="img-wrap">
						<img :src="item.spic" >	
					</div>
					<div class="product-info">
						<p class="ellipsis2">{{item.goodName}}</p>
						<p class="justify"><span class="type">{{item.type}}</span></p> 
					</div>
					<div class="a-sum">
						<span class="theme-color">{{item.price|aprice}}</span>
						<span>x{{item.num}}</span>
					</div>
				</li>
			</ul>
			<dl class="payway bg">
				<dt @click="showPop">支付方式</dt>
				<dd v-for="(item,index) of pay" @click="selectThis(index)" :class="[item.cls,{'selected':selected==index}]">
					{{item.name}}
				</dd>
			</dl>
			<van-popup v-model="show" :close-on-click-overlay="false">
				<div class="devTips">
					<p class="content">
					dev:后台调用{{pay[selected]["name"]}}的接口，并执行扣款操作
					</p>
					<div class="btn-area">
						<span @click="toFail">失败回调</span>
						<span @click="toSuccess">成功回调</span>
					</div>
				</div>
			</van-popup>
		</div>
		<div class="order-footer border-top">
			合计:<b class="theme-color">{{total|aprice}}</b><span @click="payMoney">立即支付</span>
		</div>
	</div>
</template>
<script>
	import backHeader from "common/backHeader";
	import  tipShow from "@/assets/js/tips.js";
	import { Popup } from 'vant';
	// import 'vant/lib/popup/style';

	import {mapState} from "vuex";
	import "@/assets/css/border.css";
	export default{
		data() {
		  return {
		    show: false,
		    pay:[
		    	{"name":"支付宝",type:1,cls:"alipay"},
		    	{"name":"微信",type:2,cls:"wechat"}
		    	],
		    selected:0
		  }
		},
		computed:{
			...mapState(['orderList','cartList']),
			payType:function() {
				return this.pay[this.selected].type;
			},
			total:function() {
				var price=0;
				this.orderList.forEach(function(ele) {
					price+=ele.price*ele.num;
				})
				return price;
			}
		},
		components:{
			backHeader,
			vanPopup:Popup
		},
		filters:{
			aprice(val){
				return '￥'+val;
			}
		},
		methods:{
			showPop:function() {
				this.show=true;
			},
			toFail:function() {
				this.show=false;
				tipShow('由于xxx原因失败,重试或返回');
			},
			toSuccess:function() {
				// 支付成功后的回调
				var selectId=this.orderList.map(function(ele) {
					return ele.cid;
				})
				selectId.map((ele)=> {
					this.cartList.forEach((cartele,index)=> {
						if(cartele.cid==ele){
							// this.cartList.splice(index,1)
							this.$store.commit("deleteGoods",index);
						}
					})
				})
				this.bus.$emit("clearCheck");
				this.$store.commit("removeOrder");
				var willgo=this.$route.query.from;
				this.$router.replace({path:"/Order/paySuccess",query:{from:willgo}});
			},
			selectThis (i){
				this.selected=i;
			},
			payMoney:function() {
				this.show=true;
			}
		},
		beforeRouteLeave:function(to,from,next) {
			if(to.name=="Cart"||to.name=="rentDetail"){
				this.$store.commit("removeOrder");
			}
			next()
		}
	}
</script>
<style scoped>
	.amount{
		font-size: 16px;
	}
	.devTips{
		width: 250px;
		padding: 10px;
	}
	.product-info{
		flex: 1;
	}
	.a-sum{
		min-width: 50px;
		text-align: right;
	}
	.a-sum span:nth-child(2){
		display: block;
		color: #999;
	}
	.order-footer{
		height: 50px;
		position:fixed;
		width: 100%;
		padding: 0px 3%;
		box-sizing: border-box;
		bottom: 0px;
		background: #fff;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
	.order-footer span{
		background: #e6292c;
		color: #fff;
		text-align: center;
		line-height: 36px;
		margin: 7px 0px 7px 12px;
		height: 36px;
		width: 100px;
		display:block;
		border-radius: 10px;
	}
	.center-wrap{
		width: 94%;
		margin: 0 auto;
	}
	.type{
		color: #999;
		background: #eee;
		border-radius: 4px;
		display: inline-block;
		margin-top: 5px;
		padding: 2px 5px;
	}
	.bg{
		background: #fff;
		padding: 2px 10px;
		margin: 10px 0px;
		line-height: 30px;
		border-radius: 10px;
	}
	li{
		display: flex;
		padding:15px 10px;
		margin: 10px 0px;
		border-radius: 10px;
		background: #fff;
		align-items: center;
	}
	.info-footer{
		display: flex;
		justify-content:space-between;
		align-items: center;
	}
	.theme-color{
		color: #e6292c;
	}
	.img-wrap{
		width: 20vw;
		height: 20vw;
		flex-shrink: 0;
		margin: 0px 10px;
	}
	.img-wrap img{
		width: 100%;
		height: 100%;
	}
	.payway dd{
		margin: 10px 0;
	}
	.payway dd:before{
		width: 30px;
		height: 30px;
		content: "";
		display: inline-block;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 26px;
		margin-right: 4px;
		vertical-align: middle;
	}
	.payway dd{
		/*padding-left: 40px;*/
	}
/*	.payway dd.selected{
		background: #eee;
	}*/
	.payway dd.selected:after{
		/*content: "√";*/
		content: "✔";
		float:right;
		color: #e6292c;
		font-size: 22px;
	}
	.alipay:before{
		background-image: url(../../assets/img/aliPay.png);
	}
	.wechat:before{
		background-image: url(../../assets/img/weChat.png);
	}
	.btn-area{
		display:flex;
	}
	.btn-area span{
		flex: 1;
		text-align: center;
		line-height: 38px;
	}
	.btn-area span:nth-child(1){
		color: #999;
	}
	.btn-area span:nth-child(2){
		color: #e6292c;
	}
	.devTips .content{
		padding: 20px;
	}
</style>


